<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from '../在线考试列表/components/TopButtons'
    import {getExamResult, getUserExamResultList, deleteExamResult} from '@/api/examResult'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            TopButtons, tableComponent, filterComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '试卷名称',
                        dataIndex: 'paper.name',
                        key: 'paper.name',
                    },
                    {
                        title: '分数',
                        dataIndex: 'score',
                        key: 'score',
                    },
                    {
                        title: '是否通过',
                        dataIndex: 'is_passed_format',
                        key: 'is_passed_format',
                    },
                    {
                        title: '开始答题时间',
                        dataIndex: 'created_at',
                        key: 'created_at',
                    },
                    {
                        title: '交卷时间',
                        dataIndex: 'updated_at',
                        key: 'updated_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getUserExamResultList,
                filterFields: [
                    {label: '考试开始时间', field: 'start_timestamp'},
                    {label: '-', field: 'end_timestamp'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCheck(id) {
                this.$router.push({name: '在线考试查看', params: {id: id}})
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.handleCheck(row.id);
                        break;
                }
            },
        },
    }
</script>
